@import '../../theme/core.scss';

  button {
	text-transform: uppercase;
  }
  
  button {
	position: relative;
	border: none;
	font-size: 1.15rem;
	font-family: AvenirBlack;
	text-shadow: 2.5px 1.5px 0px $integrity, 9px 8px 0px rgba(0,0,0,0.15);
	transition: color 0.5s, transform 0.2s, background-color 0.2s;
	outline: none;
	border-radius: 3px;
	margin: 0 10px;
	padding: 23px 33px;
	border: 3px solid transparent;
  }
  button:active {
	transform: translateY(3px);
  }
  button::after, button::before {
	border-radius: 2px;
  }
  
  .shrink-border {
	background-color: transparent;
	backdrop-filter: blur(2px);
	color:$unity;
  }
  .shrink-border:hover {
	background-color: transparent;
	box-shadow: none;
	color: $integrity;
	text-shadow: 2.5px 1.5px 0px $unity, 9px 8px 0px rgba(0,0,0,0.15);
  }
  .shrink-border::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: 3px solid $integrity;
	transition: opacity 0.3s, border 0.3s;
  }
  .shrink-border:hover::before {
	opacity: 0;
  }
  .shrink-border::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: transparent;
	border: 3px solid $unity;
	opacity: 0;
	z-index: -1;
	transform: scaleX(1.1) scaleY(1.3);
	transition: transform 0.3s, opacity 0.3s;
  }
  .shrink-border:hover::after {
	opacity: 1;
	transform: scaleX(1) scaleY(1);
  }
  
  .material-bubble {
	background-color: transparent;
	color: #1b1b20;
	border: none;
	overflow: hidden;
	box-shadow: none;
  }
  .material-bubble:hover {
	color: #e6e6e6;
  }
  .material-bubble::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: 3px solid #1b1b20;
	transition: opacity 0.3s, border 0.3s;
  }
  .material-bubble:hover::before {
	opacity: 0;
  }
  .material-bubble::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 200px;
	height: 200px;
	background-color: #494958;
	border-color: transparent;
	border-radius: 50%;
	transform: translate(-10px, -70px) scale(0.1);
	opacity: 0;
	z-index: -1;
	transition: transform 0.3s, opacity 0.3s, background-color 0.3s;
  }
  .material-bubble:hover::after {
	opacity: 1;
	transform-origin: 100px 100px;
	transform: scale(1) translate(-10px, -70px);
  }